
.analog-clock {
  margin: 50px auto;
  position: relative;
  width: 250px;
  height:250px;
  border-radius: 50%;
  background: url(../../../public/img/clockframe.png) no-repeat 50% 50% / cover ;
}

.analog-clock > .hand {
  --deg: 0;
  position: absolute;
  bottom: 50%;
  left: 50%;
  border-radius: 10px;

  transform-origin: bottom;
  transition: .1s;
  transform: translate3D(-50%, 0, 0) rotate(calc(var(--deg) * 1deg));
  z-index: 10;
}

.analog-clock > .hand.second {
  width: 4px;
  height: 80px;
  background-color: #FF6D60;
}
.analog-clock > .hand.minute {
  width: 6px;
  height: 70px;
  background-color: #98D8AA;
}
.analog-clock > .hand.hour {
  width: 8px;
  height: 40px;
  background-color: #F7D060;
}

