* {
  font-family: 'Noto Sans';
}

button {
  border: none;
  transition: .3s;
  transform-origin: center;

}

button:hover {
  transform-origin: center;
  transform: scale(1.1);
}

p,
strong,
button {
  margin: initial;
  padding: initial;
}

body {
  background-color: #5B2386;
}

.wrapper {
  text-align: center;
}


