.a11y-hidden {
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
}

.main-form {
  color: #fff;
  font-size: 24px;
  
}

.main-form > div {
  margin-top: 102px;
}

.main-form > div+div {
  margin-top: 27px;
}

#inpt-goal,
#inpt-planning {
  margin: 0 10px;
  padding: 40px;
  font-size: 24px;
  font-weight: 400;
  box-sizing: border-box;
  border: 0;
  width: 228px;
  height: 57px;
  border-radius: 7px;
}

#inpt-goal::placeholder,
#inpt-planning::placeholder{
  color: #BABCBE;

}

.submit-btn {
  position: relative;
  font-weight: 700;
  font-size: 24px;
  color: #5B2386;
  width: 565px;
  height: 66px;
  background: #FCEE21;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
  border-radius: 56px;
  margin-top: 115px;
  margin-bottom: 147px;
}

.submit-btn::after {
  position: absolute;
  content: '';
  right: -65px;
  top: 10px;
  width: 64px;
  height: 72px;
  background: url('../../../public/img/click.png');
}

