.navigation-btn,
.Share-btn {
  margin: 0 20px;
  font-weight: 700;
  font-size: 24px;
  color: #5B2386;
}

.navigation-btn {
  width: 356px;
  height: 66px;
  background: #FCEE21;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
  border-radius: 56px;
}

.Share-btn {
  width: 191px;
  height: 66px;
  background: #FFFFFF;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
  border-radius: 56px;
}
