.clock-loader {
  animation-name: rotate;
  animation-duration: 2s;
  transform: rotate(0deg);
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}

body {
  align-items: center;
  display: flex;
  justify-content: center;
  min-height: 100vh;
}

.result {
  margin-bottom: 148px;
}

.result-planning{
  margin-bottom: 30px;
}

.result-goal,
.result-planning {
  font-weight: 400;
  font-size: 24px;
  color: #fff;
}

.result-goal strong,
.result-planning strong{
  padding: 0 10px;
  vertical-align: middle;
  font-weight: 700;
  font-size: 72px;
}