.modal {
  width: 800px;
  height: 800px;
  padding: 40px;
  box-sizing: border-box;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #FFFFFF;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.2);
  border-radius: 30px;
}



.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal-message,
.modal-submessage{
  font-family: 'Nanum Brush Script';
  color: #5B2386;
}

.modal-message{
  font-size: 96px;
}

.modal-submessage {
  font-size: 36px;
  margin-top: 20px;
  margin-bottom: 50px;
}

.licat-img {
  margin-bottom: 73px;
  width: 433px;
}

.close-btn {
  width: 508px;
  height: 66px;
  background: #FCEE21;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
  border-radius: 56px;
  color: #5B2386;
  font-weight: 700;
  font-size: 24px;
}